* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: rgba(15, 15, 17, 0.9);
  backdrop-filter: blur(6px);
  color: var(--color-text);
  font-family: var(--font-family);
  line-height: 1.6;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}